<script>
    export default {
        name: "price-component",
        data() {
            return {
                show: false
            }
        },
        methods: {
            checkImgUp: function () {
                //             
                //        opacity  0  0.5
                this.show = (document.getElementById('splinter').offsetTop < window.pageYOffset
                    && window.pageYOffset < document.getElementById('footer').offsetTop - document.documentElement.clientHeight);
            },
            scrollToSlow: function (elemAttribute, heightSub) {
                let heightScroll = document.getElementById(elemAttribute).offsetTop + document.documentElement.clientHeight - heightSub; //   +   - 50
                window.scrollTo({
                    top: heightScroll,
                    behavior: "smooth" //   
                });
            }
        },
        created() {
            window.addEventListener('scroll', this.checkImgUp);
        },
        mounted() {
            let elements = document.getElementsByClassName('linkTo');
            for (let i = 0; i < elements.length; i++) {
                let that = this;
                let element = elements[i];
                let elementAttr = element.getAttribute('data-scroll-to');
                element.addEventListener('click', (function (e) {
                        e.preventDefault();  //    
                        let heightSub = (document.documentElement.clientWidth < 813) ? 62 : 50;//     50px (-  )
                        //     62 px (  2px   50 px  padding 10 px)
                        that.scrollToSlow(elementAttr, heightSub);//  ,  
                    }
                ));
            }
        },
        updated() {
            document.getElementById('fixed_price_img').addEventListener('click', function (e) {
                e.preventDefault();  //    
                (function () { //    
                    let heightSub = (document.documentElement.clientWidth < 813) ? 50 : 0;// PriceComponent
                    let heightScroll = document.getElementById('splinter').offsetTop - heightSub; //   +  
                    window.scrollTo({
                        top: heightScroll,
                        behavior: "smooth" //   
                    });
                }())
            });
        },
        destroyed() {
            window.removeEventListener('scroll', this.checkImgUp)
        }
    }
</script>

